  top: 38px;
  left: 46px;
  content: "";
}
@media (max-width: 1023px) {
  .banner-card__item--users svg {
    display: none;
  }
}
.banner-card__item--courses svg {
  position: absolute;
  top: 42px;
  left: 46px;
  content: "";
}
@media (max-width: 1023px) {
  .banner-card__item--courses svg {
    display: none;
  }
}
.banner-card__item--mentors svg {
  position: absolute;
  top: 42px;
  left: 49px;
  content: "";
}
@media (max-width: 1023px) {
  .banner-card__item--mentors svg {
    display: none;
  }
}
.banner-card__item.is-active {
  background-color: #486dd8;
  pointer-events: none;
}
.banner-card__item.is-active::before {
  background-color: #567ae4;
  border: none;
}
.banner-card__item.is-active svg {
  fill: #ffffff;
}
.banner-card__item.is-active h3 {
  color: #ffffff;
}
.banner-card__item.is-active p {
  color: #97b2fe;
}
@media (hover: hover) {
  .banner-card__item:hover:not(.focus-visible) {
    background-color: #dde6ff;
  }
  .banner-card__item:hover:not(.focus-visible)::before {
    background-color: #edf2ff;
  }
  .banner-card__item:hover:not(.focus-visible) svg {
    fill: #839dfb;
  }
}
.banner-card__item.focus-visible:focus {
  background-color: #dde6ff;
}
.banner-card__item.focus-visible:focus::before {
  background-color: #edf2ff;
}
.banner-card__item.focus-visible:focus svg {
  fill: #839dfb;
}

.is-open {
  display: block;
}

.buttons {
  display: none;
  justify-content: center;
  column-gap: 34px;
}
@media (max-width: 767px) {
  .buttons {
    display: flex;
  }
}
.buttons__inner {
  position: relative;
  width: 50px;
  height: 50px;
  background-color: #f8f7fc;
  border-radius: 50%;
  border: 1px solid #efefef;
}
.buttons__inner svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.buttons__inner--prev {
  transform: rotate(180deg);
}

.button {
  position: relative;
  width: 50px;
  height: 50px;
  background-color: rgba(255, 255, 255, 0);
  z-index: 1;
}

